import { OpenBet } from '../../../models/BetOrder';


const fetchOpenBet = (req, res) => {
	OpenBet.find({ 
		'owner.superAgent': req.user._id 
	}).populate({ 
		path: 'owner.player', 
		select: 'account.username'
	}).then(openBets => { res.json(openBets) }).catch(err => { throw err })
}

export default fetchOpenBet;